.stars {
  width: 100%;
  position: relative;
  z-index: 100;
}

.shooting-stars {
  z-index: 100;
  width: 5px;
  height: 85px;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  position: absolute;
  bottom: 25vh;
  right: -5px;
  background: linear-gradient(to top, rgba(255, 255, 255, 0), white);
  animation: animShootingStar 20s linear infinite;
  animation-delay: 4s;
}

@keyframes animShootingStar {
  0% {
    transform: translateY(0px) translateX(0px) rotate(-45deg);
    opacity: 1;
    height: 5px;
  }
  20%, 100% {
    transform: translateY(-2560px) translateX(-2560px) rotate(-45deg);
    opacity: 1;
    height: 800px;
  }
}
.star:nth-child(0) {
  width: 1px;
  height: 1px;
  z-index: 1;
  background: white;
  border-radius: 50%;
  position: absolute;
  opacity: 1;
  margin-left: 992px;
  margin-top: 665px;
  animation: twinkle-0 1s linear infinite;
  animation-delay: 0s;
}

@keyframes twinkle-0 {
  from {
    opacity: 1;
